@import url(https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-gradient(
    linear,
    left bottom, left top,
    from(#928f91),
    color-stop(#9b999b),
    color-stop(#a5a3a5),
    color-stop(#aeadb0),
    color-stop(#b8b7ba),
    color-stop(#c1c0c3),
    color-stop(#cacacc),
    color-stop(#d3d3d5),
    color-stop(#dededf),
    color-stop(#e9e9ea),
    color-stop(#f4f4f4),
    to(#ffffff)
  );
  background-image: linear-gradient(
    to top,
    #928f91,
    #9b999b,
    #a5a3a5,
    #aeadb0,
    #b8b7ba,
    #c1c0c3,
    #cacacc,
    #d3d3d5,
    #dededf,
    #e9e9ea,
    #f4f4f4,
    #ffffff
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.superContainer {
  position: absolute;
  background-image: -webkit-gradient(
    linear,
    left bottom, left top,
    from(#928f91),
    color-stop(#9b999b),
    color-stop(#a5a3a5),
    color-stop(#aeadb0),
    color-stop(#b8b7ba),
    color-stop(#c1c0c3),
    color-stop(#cacacc),
    color-stop(#d3d3d5),
    color-stop(#dededf),
    color-stop(#e9e9ea),
    color-stop(#f4f4f4),
    to(#ffffff)
  );
  background-image: linear-gradient(
    to top,
    #928f91,
    #9b999b,
    #a5a3a5,
    #aeadb0,
    #b8b7ba,
    #c1c0c3,
    #cacacc,
    #d3d3d5,
    #dededf,
    #e9e9ea,
    #f4f4f4,
    #ffffff
  );
  padding: 15px;
}

.MainContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  position: relative;
  margin: 10px;
  padding-left: 25px;
}

header {
  text-align: center;
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: 25px;
  padding-top: 10px;
  padding-bottom: 0px;
  padding: 15px;
  margin: 20px;
  color: black;
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#675760),
    color-stop(#736872),
    color-stop(#807983),
    color-stop(#8e8a94),
    color-stop(#9c9ca5),
    color-stop(#a5a8b0),
    color-stop(#afb4bb),
    color-stop(#bac0c6),
    color-stop(#c1c9ce),
    color-stop(#c9d3d6),
    color-stop(#d1dcdd),
    to(#dae5e4)
  );
  background-image: linear-gradient(
    to bottom,
    #675760,
    #736872,
    #807983,
    #8e8a94,
    #9c9ca5,
    #a5a8b0,
    #afb4bb,
    #bac0c6,
    #c1c9ce,
    #c9d3d6,
    #d1dcdd,
    #dae5e4
  );
  border-radius: 25px;
}

@media screen and (max-width: 900px) {
  .MainContainer {
    display: block;
    margin: 20px auto;
    position: relative;
    width: 100%;
    height: auto;
    padding: 20px;
    margin-right: 0px;
  }

  header {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 5px;
    margin: 25px;
    margin-right: 25px;
  }
}

.VerticalBar {
  position: relative;
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(#66f8f8),
    color-stop(#68fce6),
    color-stop(#79fecf),
    color-stop(#93feb5),
    color-stop(#b1fd9a),
    color-stop(#c2fb92),
    color-stop(#d2f98c),
    color-stop(#e1f687),
    color-stop(#e4f591),
    color-stop(#e7f49b),
    color-stop(#eaf3a5),
    to(#ecf2af)
  );
  background-image: linear-gradient(
    to right,
    #66f8f8,
    #68fce6,
    #79fecf,
    #93feb5,
    #b1fd9a,
    #c2fb92,
    #d2f98c,
    #e1f687,
    #e4f591,
    #e7f49b,
    #eaf3a5,
    #ecf2af
  );
  border-radius: 25px;
  margin-left: 5px;
  margin-top: 10px;
  margin-right: 15px;
  padding: 10px;
  box-shadow: -19px 20px 34px 2px rgba(129, 235, 187, 1);
}

.HorizontalBar {
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(#66f8f8),
    color-stop(#68fce6),
    color-stop(#79fecf),
    color-stop(#93feb5),
    color-stop(#b1fd9a),
    color-stop(#c2fb92),
    color-stop(#d2f98c),
    color-stop(#e1f687),
    color-stop(#e4f591),
    color-stop(#e7f49b),
    color-stop(#eaf3a5),
    to(#ecf2af)
  );
  background-image: linear-gradient(
    to right,
    #66f8f8,
    #68fce6,
    #79fecf,
    #93feb5,
    #b1fd9a,
    #c2fb92,
    #d2f98c,
    #e1f687,
    #e4f591,
    #e7f49b,
    #eaf3a5,
    #ecf2af
  );
  border-radius: 25px;
  margin-left: 20px;
  margin-top: 10px;
  padding: 10px;
  box-shadow: -19px 20px 34px 2px rgba(129, 235, 187, 1);
}

.recharts-cartesian-axis-tick {
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: large;
  font-weight: bold;
}

.table-chart {
  margin-left: 30px;
  margin-right: 30px;
  padding: 60px;
}

.boo {
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: large;
  font-weight: bold;
  border-radius: 25px;
}

.ul {
  display: flex;
  justify-content: space-around;

  font-family: "Yanone Kaffeesatz", sans-serif;
}

.li {
  list-style: none;
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(#928f91),
    color-stop(#9b999b),
    color-stop(#a5a3a5),
    color-stop(#aeadb0),
    color-stop(#b8b7ba),
    color-stop(#c1c0c3),
    color-stop(#cacacc),
    color-stop(#d3d3d5),
    color-stop(#dededf),
    color-stop(#e9e9ea),
    color-stop(#f4f4f4),
    to(#ffffff)
  );
  background-image: linear-gradient(
    to right,
    #928f91,
    #9b999b,
    #a5a3a5,
    #aeadb0,
    #b8b7ba,
    #c1c0c3,
    #cacacc,
    #d3d3d5,
    #dededf,
    #e9e9ea,
    #f4f4f4,
    #ffffff
  );
  padding: 12px;
  border-radius: 25px;
  font-weight: bold;
}

